import { Component, OnInit } from '@angular/core';
import{Http}from'@angular/http';

import { Router, ActivatedRoute } from '@angular/router';
import { Post} from 'app/post/post.entity';
import { User } from  'app/user/user.entity';
import { ApiPostsService } from "app/api-posts.service";
import { LoginService } from "app/login.service";

const URL = 'http://localhost:8080/api';

@Component({
  selector: 'app-user-post',
  templateUrl: './user-post.component.html',
  styleUrls: ['./user-post.component.css']
})
export class UserPostComponent implements OnInit {

  private posts:Post[]=[];
  username:string;
  private user : User = {
      id: 0,
      username: "Cargando...",
      userEmail: "Cargando...",
      userBiography: "Cargando...",
      userLocation: "Cargando...",
      userLink: "",
      report: false,
      roles: [],
      userFollowing: [],
      userFollowers: [],
      userPosts: []
    };

  constructor(private http: Http, private route: ActivatedRoute, private router: Router, private apiPostsService: ApiPostsService, private loginService:LoginService) {
    this.username = route.snapshot.params['name'];
    this.getUserInfo();
    this.getUserPosts();
   }

  ngOnInit() {
  }
  getUserInfo(){
    let url=URL + "/users/name="+this.username;
      this.http.get(url).subscribe(
  response=>{
  letdata=response.json();
  this.user=data;
      },
  error=>console.error(error)
  );
  }
  getUserPosts(){
    let url=URL + "/posts/user="+this.username+"/";
        this.http.get(url).subscribe(
    response=>{
    letdata=response.json();
            this.posts=data;
            console.log(data);
        },
    error=>console.error(error)
    );
    }

}



